/* src/components/SocialIcons/SocialIcons.css */

.social-icons {
  position: fixed;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 999;
}

/* Icon links */
.social-link {
  margin: 0.5rem 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

/* Rotated "follow me" text */
.follow-me {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  margin-top: 1rem;
  font-size: 0.8rem;
}

/* Media query for larger screens to ensure icons don't overlap with content */
@media (min-width: 1200px) {
  .social-icons {
    right: 1rem;
  }
}

/* Media query for smaller screens */
@media (max-width: 1024px) {
  .social-icons {
    right: 1rem;
  }
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  .social-icons {
    position: static;
    flex-direction: row;
    margin-top: 2rem;
    transform: none;
    justify-content: center;
    width: 100%;
  }
  
  .social-link {
    margin: 0 0.5rem;
  }
  
  .follow-me {
    writing-mode: horizontal-tb;
    margin-top: 0;
    margin-right: 1rem;
  }
}

/* Small Mobile Adjustments */
@media (max-width: 480px) {
  .social-icons {
    bottom: 1rem;
    right: 50%;
    transform: translateX(50%);
    gap: 1.2rem;
    padding: 0.4rem 0.8rem;
  }

  .social-icons a {
    font-size: 0.9rem;
  }
}