.use-case-container {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

.use-case-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  position: sticky;
  top: 0; /* Position right below navbar */
  z-index: 2;
  background: white;
  padding: 1rem 0;
}

.use-case-title {
  font-size: 1.5rem;
  margin: 0;
}

.navigation-buttons {
  display: flex;
  gap: 1rem;
}

.nav-button {
  padding: 0.5rem 1rem;
  border: 1px solid #ddd;
  background: white;
  cursor: pointer;
  border-radius: 4px;
}

.nav-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Section styles */
.use-case-section {
  margin-bottom: 3rem;
  scroll-margin-top: 130px; /* Increased to account for sticky header */
  padding-top: 2rem; /* Add space below the floating header */
}

.section-title {
  font-size: 1.5rem;
  color: #aaa;
  margin: 0 0 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #000;
}

.section-title .highlight-blue {
  color: #0074f0;
  font-weight: bold;
}

/* First section should not have top margin */
#devtools-overview.use-case-section {
  padding-top: 2rem; /* Keep consistent padding for first section */
}

/* Overview section */
.overview-content {
  margin-top: 1rem;
  margin-bottom: 1.5rem;
}

.overview-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin: 1rem 0;
}

.overview-list li {
  margin-bottom: 1rem;
  line-height: 1.5;
}

/* Specification section */
.specification-content {
  margin-top: 2rem;
}

.tool-name {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

.spec-details {
  margin-bottom: 0;
}

.spec-row {
  display: flex;
  margin-bottom: 0.5rem;
}

.spec-label {
  font-weight: bold;
  width: 140px;
}

.goal-text {
  font-style: italic;
  line-height: 1.5;
  color: #333;
}

/* Problem section styles */
.problem-header {
  margin-bottom: 1.5rem;
}

.highlight-blue {
  color: #0074f0;
}

.highlight-blue h3 {
  font-weight: bold;
}

.problem-content {
  font-size: 1rem;
  line-height: 1.6;
  padding-top: 1rem;
}

.problem-content p {
  margin-bottom: 1rem;
}

.impact-section {
  margin-top: 2rem;
}

.impact-section h3 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.impact-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-top: 1rem;
}

.impact-list li {
  margin-bottom: 0.75rem;
  line-height: 1.5;
}

.process-content {
  margin-top: 1rem;
}

.process-content h3 {
  color: #0074f0;
  font-weight: bold;
  font-size: 1.2rem;
  margin: 2rem 0 0.5rem;
}

.process-content p {
  margin-bottom: 1rem;
  line-height: 1.5;
}

.process-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin: 0.5rem 0 1.5rem;
}

.process-list li {
  margin-bottom: 1rem;
  line-height: 1.5;
}

.solution-content {
  margin-top: 1rem;
}

.solution-image {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 1rem 0 2rem;
  border-radius: 4px;
}

.solution-content p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.solution-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin: 1rem 0 2rem;
}

.solution-list li {
  margin-bottom: 0.75rem;
  line-height: 1.5;
}

.card-sorting-section {
  margin-top: 2rem;
}

.card-sorting-section h3 {
  display: inline;
  margin-right: 0.5rem;
}

.card-sorting-image {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 1.5rem 0;
  border-radius: 4px;
  border: 1px solid #eee;
}

.design-features-content {
  margin-top: 1rem;
}

.design-features-content h3 {
  color: #0074f0;
  font-size: 1.2rem;
  margin: 2rem 0 1rem;
}

.design-features-content p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.wireframe-image {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 1rem 0 1.5rem;
  border-radius: 4px;
  border: 1px solid #eee;
}

.contributions-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin: 1rem 0 2rem;
}

.contributions-list li {
  margin-bottom: 1rem;
  line-height: 1.5;
}

.conclusion-content {
  margin-top: 1rem;
}

.conclusion-content p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  font-size: 1rem;
}

.conclusion-content p:last-child {
  margin-bottom: 0;
}