.role-list {
  list-style-type: disc;
  padding: 0;
  margin-left: 1.5rem;
}

.role-list li {
  margin-bottom: 1rem;
  font-size: 1rem;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .section-container {
    padding: 1rem;
  }
  
  .role-list li {
    margin-bottom: 1.5rem;
  }
} 