.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  background: #fff;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  z-index: 1000;
}

.navbar-left h1 {
  font-size: 1.2rem;
  margin-bottom: 0.25rem;
}

.navbar-left p {
  font-size: 0.9rem;
  color: #555;
}

.navbar-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.navbar-right a {
  text-decoration: none;
  color: #000;
  font-size: 1rem;
}

/* The NavLink adds an 'active-link' class when the route is active */
.active-link {
  color: #0074f0 !important;
  font-weight: bold;
}

.navbar-right select {
  padding: 0.25rem;
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  .navbar {
    width: 100%;
    padding: 0.75rem 1.5rem;
    height: auto;
    min-height: 70px;
    align-items: center;
  }

  .navbar-left {
    max-width: 35%;
  }

  .navbar-left h1 {
    font-size: 1rem;
    margin-top: 0;
  }

  .navbar-left p {
    font-size: 0.8rem;
    margin-bottom: 0;
  }

  .navbar-right {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
    max-width: 60%;
  }

  .navbar-right a {
    font-size: 0.85rem;
    flex: 1;
    text-align: center;
    min-width: auto;
    padding: 0 0.2rem;
    white-space: normal;
    line-height: 1.2;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .navbar-right select {
    font-size: 0.85rem;
    padding: 0.2rem;
    flex: 0 0 auto;
    margin-left: 0.3rem;
  }
}

/* Small Mobile Adjustments */
@media (max-width: 480px) {
  .navbar {
    padding: 0.5rem 1rem;
    min-height: 65px;
  }

  .navbar-left {
    max-width: 38%;
  }

  .navbar-left h1 {
    font-size: 0.9rem;
  }

  .navbar-left p {
    font-size: 0.75rem;
  }

  .navbar-right {
    gap: 0.2rem;
    max-width: 58%;
  }

  .navbar-right a {
    font-size: 0.8rem;
    padding: 0 0.15rem;
  }

  .navbar-right select {
    font-size: 0.8rem;
    padding: 0.2rem;
  }
}