.sidebar {
  position: fixed;
  top: 70px; /* below the navbar */
  left: 0;
  width: 220px;
  bottom: 0;
  background: #fff;
  border-right: 1px solid #ccc;
  padding: 1rem;
}

.sidebar ul {
  list-style: none;
  padding-left: 0;
}

.sidebar ul li {
  margin-bottom: 1rem;
}

/* Default link style */
.sidebar ul li a {
  text-decoration: none;
  color: #000;
}

/* Highlight the active li in a special color or style */
.sidebar ul li.active > a {
  color: #007bff;
  font-weight: bold;
  text-decoration: underline;
}
