/* src/pages/About.css */
.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  font-family: Arial, sans-serif;
}

.main-content {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 3rem;
  align-items: start;
  margin-top: 100px;
}

.photo-section img {
  width: 100%;
  border-radius: 4px;
}

.text-section {
  padding-right: 2rem;
}

.text-section h2 {
  font-size: 3.5rem;
  font-weight: normal;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.text-section p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  color: #333;
}

.blue-text {
  color: #007bff;
}

/* Tablet Adjustments */
@media (max-width: 1024px) {
  .main-content {
    grid-template-columns: 300px 1fr;
    gap: 2rem;
  }

  .text-section h2 {
    font-size: 3rem;
  }
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  .about-container {
    padding: 1rem;
  }

  .main-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    margin-top: 50px;
  }

  .photo-section {
    max-width: 250px;
    margin: 0 auto;
  }

  .text-section {
    padding-right: 0;
  }

  .text-section h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 1.5rem;
  }

  .text-section p {
    font-size: 1rem;
    margin-bottom: 1.2rem;
  }
}

/* Small Mobile Adjustments */
@media (max-width: 480px) {
  .about-container {
    padding: 1rem;
    padding-top: 60px;
  }

  .main-content {
    margin-top: 30px;
    gap: 1.5rem;
  }

  .text-section h2 {
    font-size: 2rem;
    margin-bottom: 1.2rem;
  }

  .photo-section {
    max-width: 100%;
  }
}