.cv-container {
  max-width: 1200px;
  margin: 80px auto;
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cv-header {
  width: 100%;
  margin-bottom: 1rem;
  border-bottom: 1px solid #000;
  padding-bottom: 0.5rem;
}

.cv-title {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

/* Left side content - Experience section */
.cv-left {
  width: 48%;
}

.cv-right {
  width: 48%;
}

.cv-section {
  margin-bottom: 2rem;
}

.cv-section-title {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  color: #0074f0; /* Blue color for section titles - matched from Work page */
  font-weight: normal;
}

/* Job entry */
.cv-job {
  margin-bottom: 1.5rem;
}

.cv-job-title {
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.cv-job-location {
  font-style: italic;
  margin-bottom: 0.25rem;
}

.cv-job-date {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.cv-job-description {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-top: 0.5rem;
}

.cv-job-description li {
  margin-bottom: 0.5rem;
}

/* Education section */
.cv-education-entry {
  margin-bottom: 1rem;
}

.cv-education-title {
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.cv-education-institution {
  margin-bottom: 0.25rem;
}

.cv-education-detail {
  font-size: 0.9rem;
  margin-bottom: 0.25rem;
}

/* Summary section */
.cv-summary {
  margin-bottom: 1.5rem;
}

.cv-summary-title {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.cv-summary-highlight {
  font-weight: bold;
}

.cv-summary p {
  margin-bottom: 0.75rem;
}

/* Skills section */
.cv-skills-list {
  margin-bottom: 1rem;
}

/* Tools section */
.cv-tools-list {
  list-style-type: disc;
  padding-left: 1.5rem;
}

.cv-tools-list li {
  margin-bottom: 0.25rem;
}

/* Trainings section */
.cv-trainings-list {
  list-style-type: disc;
  padding-left: 1.5rem;
}

.cv-trainings-list li {
  margin-bottom: 0.5rem;
}

.cv-training-name {
  font-weight: bold;
}

.cv-training-date {
  font-size: 0.8rem;
  color: #666;
}

.cv-training-instructor {
  font-size: 0.9rem;
  margin-top: 0.25rem;
  color: #333;
}

/* Languages section */
.cv-language {
  margin-bottom: 0.25rem;
}

.cv-language-name {
  font-weight: bold;
}

@media (max-width: 768px) {
  .cv-container {
    flex-direction: column;
    margin: auto;
  }
  
  .cv-left, .cv-right {
    width: 100%;
  }

  .cv-title {
    font-size: 1.1rem;
  }

  .cv-section-title {
    font-size: 1.2rem;
  }

  .cv-job-title, .cv-education-title {
    font-size: 0.95rem;
  }
} 