/* src/App.css */

/* Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Use a smooth scroll effect for anchor links */
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Helvetica Neue', Arial, sans-serif; /* or your preferred font */
  color: #333;
}

/* The container for your entire app */
.app-container {
  position: relative;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
}

/* Fixed navbar wrapper */
.navbar-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
}

/* Main layout: sidebar + content side by side */
.main-layout {
  display: flex;
  flex-direction: row;
  height: 100vh; /* Fill viewport height */
  margin-top: 60px; /* Enough space for the fixed navbar (assuming ~60px) */
}

/* The scrollable content area (right side) */
.content-area {
  flex: 1; /* take remaining width */
  overflow-y: auto; /* enable vertical scroll */
  padding: 2rem;
  position: relative; /* so children can be absolutely positioned if needed */
}

/* Right-side fixed social icons container */
.social-fixed {
  position: fixed;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  /* Stack sidebar above content area on small screens */
  .main-layout {
    flex-direction: column;
    height: auto;
  }

  .content-area {
    height: auto;
    overflow-y: visible;
  }

  /* Make social icons not fixed or reposition them on mobile */
  .social-fixed {
    position: static;
    transform: none;
    margin: 1rem 0;
    flex-direction: row;
  }
}