.email-container {
  max-width: 600px;
  margin: 80px auto;
  padding: 2rem;
}

.email-header {
  margin-bottom: 2rem;
}

.email-title {
  font-size: 1.8rem;
  color: #0074f0;
  margin-bottom: 1rem;
}

.email-subtitle {
  font-size: 1.1rem;
  color: #666;
  line-height: 1.5;
  margin-bottom: 2rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 500;
  color: #333;
}

.form-group input,
.form-group textarea {
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  font-family: inherit;
}

.form-group textarea {
  min-height: 150px;
  resize: vertical;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #0074f0;
  box-shadow: 0 0 0 2px rgba(0, 116, 240, 0.1);
}

.submit-button {
  background-color: #0074f0;
  color: white;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
  align-self: flex-start;
}

.submit-button:hover {
  background-color: #0061cc;
}

.submit-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.form-message {
  padding: 1rem;
  border-radius: 4px;
  margin-bottom: 1rem;
}

.form-message.success {
  background-color: #e8f5e9;
  color: #2e7d32;
  border: 1px solid #a5d6a7;
}

.form-message.error {
  background-color: #ffebee;
  color: #c62828;
  border: 1px solid #ef9a9a;
}

@media (max-width: 768px) {
  .email-container {
    margin: 60px auto;
  }

  .email-title {
    font-size: 1.5rem;
  }

  .submit-button {
    width: 100%;
  }
} 