.process-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin: 0;
}

.process-list li {
  margin-bottom: 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .section-container {
    padding: 1rem;
  }
  
  .process-list li {
    margin-bottom: 1rem;
  }
} 