/* Overview Component Styles */

/* Project Info Section */
.project-info {
  margin: 1rem;
}

.info-row {
  display: flex;
  align-items: baseline;
}

.info-label {
  font-weight: bold;
  width: 140px;
  flex-shrink: 0;
  font-size: 1.3rem;
  color: #000;
}

.info-value {
  font-size: 1.3rem;
  color: #333;
}

.blue-text {
  color: #0074f0;
  font-weight: bold;
}

/* Key Features Section */
.subheading-blue {
  color: #0074f0;
  font-size: 1rem;
}

.list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}

.list li {
  margin-bottom: 0.5rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .info-row {
    flex-direction: column;
    margin-bottom: 1rem;
  }
  
  .info-label {
    width: 100%;
    margin-bottom: 0.25rem;
  }
} 